@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
    font-family: 'nanum';
    src : url(/static/media/NanumGothic.755c5696.otf)
}
body {
    font-family: 'nanum';
    font-weight: 600;
    color: #333;
    background-color: #2e3140;
}

.wrapper {
    display: flex;
}
.row {
    display: flex;
}
.row:first-child {
    border-top: 1px solid #80808085;

}

.row > div {
    padding: 9px 10px 6px;
}

.attribute {
    background: grey !important;
    color: whitesmoke;
}
.cell {
    border-bottom: 1px solid #80808085;
    border-right: 1px solid #80808085;
    background: whitesmoke;
}
.cell:first-child {
    border-left: 1px solid #80808085;
}
.lowest-tail {
    display: flex;
    justify-content: space-between;
    /* padding: 0px 10px; */
    box-sizing: border-box;
    margin-top: 10px;
    color: whitesmoke;
}
.honey-bee {
    background: url(/static/media/bee.13fdb4f8.png) no-repeat center center;
    width: 250px;
    height: 200px;
    float: right;
    top: 70px;
    left: 350px;
    position: absolute;
    opacity: 25%;
}
.--flex {
    display: flex;
}
.--relative {
    position: relative;
}
.--inner-eye {
    float: right;
    position: absolute;
    color: #333;
    font-size: 20px;
    margin: 7px 0 0 10px;
}
.--space-between {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-top: 1px solid whitesmoke;
    padding-top: 7px;
    margin-right: 10px;
    margin-top: 1px;
}
.--long {
    width: 280px;
}
.--short {
    width: 100px;
}
.--extra-short {
    width: 50px;
}
.--elipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.--num {
    text-align: right;
}
.--plus {
    color: red;
}
.--minus {
    color: blue;
}
.--shown-true {
    display: flex;
}
.--shown-false {
    height: 5px;
}
.--simpleInput {
    padding: 7px 10px 5px 40px;
    box-sizing: border-box;
    font-weight: 600;
    font-family: 'nanum';
    font-size: 16px;
}
.--red .cell {
    background: #ff8d8d;
}

.icon {
    margin-right: 5px;
}
.--green {
    transition: .1s color;
    color: #00b700;
}
.--white {
    transition: 1.5s color;
    color: white;
}
.--temp {
    margin-right: 10px;
}

